.footer {
  font-family: Roboto, "Helvetica Neue", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
}

.footer__content {
  display: flex;
  justify-content: space-between;
  padding: 80px 0px 24px 0px;
  margin: 0;
}

.footer__content-name {
  list-style-type: none;
  text-align: left;
}

.footer__content-year {
  list-style-type: none;
  text-align: right;
}
